<template>
    <section style="margin-left:15px;margin-right:15px;margin-top: 15px;">
      <el-card class="box-card">
        <el-menu :default-active="activeIndex" class="el-menu-demo" menu-trigger="click" mode="horizontal" router=true
        @select="handleSelect">
        <el-menu-item index="/models">模型部署与管理</el-menu-item>
        <el-menu-item index="/experiments">模型实验跟踪</el-menu-item>
        <el-menu-item index="/features">中央特征存储</el-menu-item>
      </el-menu>
      <br>
        <!--工具条-->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters" @submit.native.prevent>
                <el-form-item>
                    <el-input v-model="filters.feature_en" placeholder="$type/name-v$version" @keyup.enter.native="getFeatureList"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="getFeatureList">查询</el-button>
                </el-form-item>
                <el-form-item v-show="featureSelectType">
                    <el-select
                v-model="FeatureOrModel"
                placeholder="请选择参数"
                style="margin-left: 5px"
              >
                <!--<el-option v-for="(item,index) in dataEngines" :key="index+''" :label="item.name" :value="item.engine"></el-option>-->
                <el-option
                  v-for="(item, index) in FeatureOrModelOpts"
                  :key="index + ''"
                  :label="item.label"
                  :value="item.value"
                ></el-option>
              </el-select>

                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="handleAdd" icon="el-icon-plus">特征</el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <div>
    <span v-if="showSearch">为您检索到的模型为: <span style="color:green"><strong>{{ searchResult }}</strong></span></span>
  </div>

        <!--列表-->
        <el-table :data="features" highlight-current-row v-loading="listLoading" @selection-change="selsChange">
          <el-table-column>
            <template slot-scope="scope">
              <el-card class="box-card" shadow="hover">
            <el-descriptions class="margin-top" title="" :column="3" :size="size">
              <template slot="title">
              <router-link  :to="{ name: '特征管理', params: {feature_id: scope.row.id}}"
                  style="cursor:pointer;color: #0000FF;text-decoration:underline">
                        {{ scope.row.feature_cn }}
              </router-link>
            </template>

               <template slot="extra">
                  <el-button type="text" size="small" style="color:#3c4ab5" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                  <el-button type="text" size="small" style="color:#808B96" @click="handleDel(scope.$index, scope.row)">删除</el-button>
              </template>
             <el-descriptions-item label="特征ID">{{scope.row.feature_en}}</el-descriptions-item>
             <el-descriptions-item label="特征类别">{{scope.row.feature_cat}}</el-descriptions-item>
             <el-descriptions-item label="创建时间">{{scope.row.created_at}}</el-descriptions-item>
             <el-descriptions-item label="最近更新">{{scope.row.updated_at}}</el-descriptions-item>
             <el-descriptions-item label="特征来源">
              <el-tag size="small" type="info">{{scope.row.gen_from}}</el-tag>
             </el-descriptions-item>
             <el-descriptions-item v-if="scope.row.feature_type==='discrete'" label="特征类型">离散型</el-descriptions-item>
             <el-descriptions-item v-else label="特征类型">连续型</el-descriptions-item>
             <el-descriptions-item v-if="scope.row.status==='production'" label="特征状态"><el-tag size="small">生产</el-tag></el-descriptions-item>
             <el-descriptions-item v-else-if="scope.row.status==='dev'" label="特征状态"><el-tag size="small" type="warning">开发</el-tag></el-descriptions-item>
             <el-descriptions-item v-else label="特征状态"><el-tag size="small" type="info">已下线</el-tag></el-descriptions-item>
             <el-descriptions-item label="特征描述">{{scope.row.description}}</el-descriptions-item>
            </el-descriptions>
          </el-card>
        </template>
      </el-table-column>
    </el-table>
    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :page-count="total" style="float:right;">
      </el-pagination>
    </el-col>
  </el-card>

        <!--编辑界面-->
        <el-dialog title="编辑特征" :visible.sync="editFormVisible" :close-on-click-modal="false" style="width: 85%; left: 12.5%">
            <el-form :model="editForm" label-width="80px"  :rules="editFormRules" ref="editForm">
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="特征名称" prop="feature_cn">
                            <el-input v-model="editForm.feature_cn" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特征ID" prop='feature_en'>
                            <el-input v-model.trim="editForm.feature_en" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="特征类型" prop='feature_type'>
                            <el-select v-model="editForm.feature_type" placeholder="请选择">
                                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特征类别" prop='feature_cat'>
                            <el-select v-model="editForm.feature_cat" placeholder="请选择">
                                <el-option v-for="item in FeatureCatOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="特征来源" prop='gen_from'>
                            <el-input v-model.trim="editForm.gen_from" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="更新频率" prop='gen_freq'>
                            <el-input v-model.trim="editForm.gen_freq" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="特征状态" prop='status'>
                            <el-select v-model="editForm.status" placeholder="请选择">
                                <el-option v-for="item in FeatureStatusOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="特征描述" prop='description'>
                    <el-input type="textarea" :rows="3" v-model="editForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
            </div>
        </el-dialog>

        <!--新增界面-->
        <el-dialog title="新增特征" :visible.sync="addFormVisible" :close-on-click-modal="false" style="width: 75%; left: 12.5%">
            <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="特征名称" prop="feature_cn">
                            <el-input v-model.trim="addForm.feature_cn" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特征ID" prop='feature_en'>
                            <el-input v-model.trim="addForm.feature_en" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="特征类型" prop='feature_type'>
                            <el-select v-model="addForm.feature_type" placeholder="请选择">
                                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="特征类别" prop='feature_cat'>
                            <el-select v-model="addForm.feature_cat" placeholder="请选择">
                                <el-option v-for="item in FeatureCatOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row :gutter="24">
                    <el-col :span="12">
                        <el-form-item label="特征来源" prop='gen_from'>
                            <el-input v-model.trim="addForm.gen_from" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="更新频率" prop='gen_freq'>
                            <el-input v-model.trim="addForm.gen_freq" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label="特征描述" prop='description'>
                    <el-input type="textarea" :rows="3" v-model="addForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="addFormVisible = false">取消</el-button>
                <el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
            </div>
        </el-dialog>
        <br>
    </section>
  </template>

<script>
// import NProgress from 'nprogress'
import $ from 'jquery'
import moment from 'moment'
import { test, addFeature, getFeatures, delFeature, updateFeature } from '@/api/api'
// import ElRow from "element-ui/packages/row/src/row";
export default {
  // components: {ElRow},
  data () {
    return {
      searchResult: '',
      featureSelectType: false,
      showSearch: false,
      FeatureOrModel: 'feature',
      FeatureOrModelOpts: [{ 'value': 'feature', 'label': '特征' }, { 'value': 'model', 'label': '模型' }],
      activeIndex: '/features',
      size: '',
      users: {},
      filters: {
        feature_en: ''
      },
      features: [],
      total: 0,
      page: 1,
      listLoading: false,
      sels: [], // 列表选中列
      editFormVisible: false, // 编辑界面是否显示
      editLoading: false,
      options: [{ label: '连续型', value: 'continue' },
        { label: '离散型', value: 'discrete' }],
      FeatureCatOptions: [{ label: '基础属性', value: '基础属性' },
        { label: '玩家行为', value: '玩家行为' },
        { label: '付费偏好', value: '付费偏好' },
        { label: '场景化', value: '场景化' }],
      FeatureStatusOptions: [{ label: '生产', value: 'production' },
        { label: '开发', value: 'dev' },
        { label: '已下线', value: 'archive' }],
      editFormRules: {
        feature_cn: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        feature_type: [
          { required: true, message: '请选择类型', trigger: 'blur' }
        ],
        feature_en: [
          { required: true, message: '请输入特征ID', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入特征描述', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 编辑界面数据
      editForm: {
        feature_cn: '',
        feature_en: '',
        status: '',
        feature_type: '',
        feature_cat: '',
        gen_freq: '',
        gen_from: '',
        description: ''
      },
      addFormVisible: false, // 新增界面是否显示
      addLoading: false,
      addFormRules: {
        feature_cn: [
          { required: true, message: '请输入特征名称', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        feature_type: [
          { required: true, message: '请选择类型', trigger: 'blur' }
        ],
        feature_en: [
          { required: true, message: '请输入特征ID', trigger: 'blur' },
          { min: 1, max: 50, message: '长度在 1 到 50 个字符', trigger: 'blur' }
        ],
        description: [
          { required: false, message: '请输入特征描述', trigger: 'blur' },
          { max: 1024, message: '不能超过1024个字符', trigger: 'blur' }
        ]
      },
      // 新增界面数据
      addForm: {
        feature_cn: '',
        feature_en: '',
        feature_type: '',
        feature_cat: '',
        gen_freq: '',
        gen_from: '',
        description: ''
      }
    }
  },
  computed: {
    tableSelected () {
      return this.FeatureOrModel
    }

  },
  watch: {
    tableSelected (fm) {
      this.changeTable(fm)
    }
  },
  methods: {
    changeTable (fm) {
      console.log(fm, 'feature_or_model')
      this.feature_or_model = fm
      this.getFeatureList()
    },
    handleSelect (key, keyPath) {
      this.activeIndex = key
      console.log(key, keyPath)
    },
    // row[column.property] 能读取到该行该列的数据。代码中实现了时间格式的转换
    formatTime2 (row, column) {
      const date = new Date(row[column.property])
      return date.getFullYear() + '-' +
                    date.getMonth() + '-' +
                    date.getDate() + ' ' +
                    date.getHours() + ':' +
                    date.getMinutes() + ':' +
                    date.getSeconds()
    },
    formatTime: function (row, column) { // 日期格式化
      var date = row[column.property]
      if (typeof date === 'undefined') {
        return ''
      }
      return moment(date).format('YYYY-MM-DD HH:mm:ss')
    },
    getUserInfo () {
      let self = this
      let param = { project_id: self.$route.params.project_id }
      $.ajax({
        type: 'get',
        // url: test+"/api/api/api_info",
        url: test + '/api/user/get_user_meta',
        async: true,
        data: param,
        headers: {
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        },
        timeout: 5000,
        success: function (data) {
          if (data.code === '999999') {
            self.users = data.data.users
            console.log(self.users, 'self.users')
          } else {
            self.$message.error({
              message: data.msg,
              center: true
            })
          }
        }
      })
    },
    // 获取项目列表
    getFeatureList () {
      this.listLoading = true
      let self = this
      let params = { page: self.page,
        user_key: JSON.parse(sessionStorage.getItem('name')),
        feature_or_model: self.FeatureOrModel,
        name: self.filters.feature_en }
      // let headers = {Authorization: 'Token '+JSON.parse(sessionStorage.getItem('token'))};
      console.log(params, 'feature_or_model')
      let headers = {
        'Content-Type': 'application/json',
        Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
      }
      getFeatures(headers, params).then((res) => {
        self.listLoading = false
        let { msg, code, data } = res
        if (code === '999999') {
          self.total = data.total
          self.features = data.data
          self.searchResult = data.searchResultModel
          self.showSearch = data.showSearch
          console.log(self.features, 'self.features')
          console.log(self.total, 'self.total')
        } else {
          self.$message.error({
            message: msg,
            center: true
          })
        }
      })
    },
    // 删除
    handleDel: function (index, row) {
      this.$confirm('确认删除该特征吗?', '提示', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true
        // NProgress.start();
        let self = this
        let params = { ids: [row.id],
          user_name: JSON.parse(sessionStorage.getItem('name')) }
        let header = {
          'Content-Type': 'application/json',
          Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
        }
        delFeature(header, params).then(_data => {
          let { msg, code } = _data
          if (code === '999999') {
            self.$message({
              message: '删除成功',
              center: true,
              type: 'success'
            })
          } else {
            self.$message.error({
              message: msg,
              center: true
            })
          }
          self.getFeatureList()
        })
      })
    },
    handleCurrentChange (val) {
      this.page = val
      this.getFeatureList()
    },
    // 显示编辑界面
    handleEdit: function (index, row) {
      this.editFormVisible = true
      this.editForm = Object.assign({}, row)
      // console.log("row",row)
    },
    // 显示新增界面
    handleAdd: function () {
      this.addFormVisible = true
    },
    // 编辑
    editSubmit: function () {
      let self = this
      this.$refs.editForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.editLoading = true
            // NProgress.start();
            let params = {
              feature_id: self.editForm.id,
              feature_cn: self.editForm.feature_cn,
              type: self.editForm.feature_type,
              feature_cat: self.editForm.feature_cat,
              description: self.editForm.description,
              gen_from: self.editForm.gen_from,
              status: self.editForm.status,
              user_name: JSON.parse(sessionStorage.getItem('name'))
            }
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            updateFeature(header, params).then(_data => {
              let { msg, code } = _data
              self.editLoading = false
              if (code === '999999') {
                self.$message({
                  message: '修改成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['editForm'].resetFields()
                self.editFormVisible = false
                self.getFeatureList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
              }
            })
          })
        }
      })
    },
    // 新增
    addSubmit: function () {
      this.$refs.addForm.validate((valid) => {
        if (valid) {
          let self = this
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            self.addLoading = true
            // NProgress.start();
            let params = JSON.stringify({
              name: self.addForm.feature_cn,
              feature_en: self.addForm.feature_en,
              feature_cat: self.addForm.feature_cat,
              type: self.addForm.feature_type,
              gen_from: self.addForm.gen_from,
              gen_freq: self.addForm.gen_freq,
              description: self.addForm.description,
              user: JSON.parse(sessionStorage.getItem('name'))
            })
            let header = {
              'Content-Type': 'application/json',
              Authorization: 'Token ' + JSON.parse(sessionStorage.getItem('token'))
            }
            addFeature(header, params).then(_data => {
              let { msg, code } = _data
              self.addLoading = false
              if (code === '999999') {
                self.$message({
                  message: '添加成功',
                  center: true,
                  type: 'success'
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getFeatureList()
              } else if (code === '999997') {
                self.$message.error({
                  message: msg,
                  center: true
                })
              } else {
                self.$message.error({
                  message: msg,
                  center: true
                })
                self.$refs['addForm'].resetFields()
                self.addFormVisible = false
                self.getFeatureList()
              }
            })
          })
        }
      })
    },
    selsChange: function (sels) {
      this.sels = sels
    }
  },
  mounted () {
    this.getFeatureList()
    // this.getUserInfo()
    /* var token = sessionStorage.getItem("token");
            var user = sessionStorage.getItem("name");
            if (token && user) {
                user = JSON.parse(user);
                this.sysUserName = user || "";
            } else {
                sessionStorage.removeItem("token");
                this.$router.push("/login");
            } */
  }
}
</script>

  <style>
  </style>
